




















import Vue from "vue";
export default Vue.extend({
  name: "Item",
  props: {
    item: Object
  },
  data: () => ({
    editing: false,
    active: false
  }),
  methods: {
    onEdit() {
      const item = this.$refs.item;
      this.editing = true;
      this.$nextTick(() => {
        const title = this.$refs.title;
        // @ts-ignore
        title.focus();
      });
    }
  }
});
