





























import Vue from "vue";
export default Vue.extend({
  name: "NewItem",
  props: {
    listIndex: Number
  },
  data: () => ({
    title: "",
    expanded: false
  }),
  methods: {
    onAddNewItem() {
      this.expanded = true;
      this.$nextTick(() => {
        // @ts-ignore
        this.$refs.title.focus();
      });
    },
    onAdd(): void {
      if (this.title) {
        this.$emit("itemAdded", {
          listIndex: this.listIndex,
          title: this.title,
          description: ""
        });
      }
      this.onCancel();
    },
    onCancel(): void {
      this.expanded = false;
      this.title = "";
    }
  }
});
