






























import Vue from "vue";
export default Vue.extend({
  name: "NewList",
  props: {
    listId: String
  },
  data: () => ({
    title: "",
    expanded: false
  }),
  methods: {
    onExpand() {
      this.$emit("navbarExpanded");
      this.expanded = true;
      this.$nextTick(() => {
        // @ts-ignore
        this.$refs.listName.focus();
      });
    },
    handleAddItem(): void {
      if (this.title) {
        this.$emit("listAdded", {
          title: this.title
        });
        this.title = "";
      }
      this.expanded = false;
    }
  }
});
